body {
  color: #444;
  background-color: #fff;
}

a {
  color: #ececec;
  text-decoration: none;
}

.busy {
  animation-name: jumpy;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.busy:nth-child(2) {
  animation-delay: .2ms;
}

.busy:nth-child(3) {
  animation-delay: .4ms;
}

@keyframes jumpy {
  0% {
    transform: translateX(-10px);
  }

  50% {
    transform: translateX(10px);
  }

  100% {
    transform: translateX(-10px);
  }
}

canvas {
  height: 100%;
  position: fixed;
}

.modal {
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  transition: visibility 0s linear .25s, opacity .25s, transform .25s;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1.1);
}

.modal-content {
  background-color: #fff;
  border-radius: .5rem;
  max-width: 90%;
  max-height: 90%;
  padding: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

.show-modal {
  opacity: 1;
  visibility: visible;
  transition: visibility linear, opacity .25s, transform .25s;
  transform: scale(1);
}

#status {
  background: none;
  height: min-content;
}

#info {
  z-index: 1;
  background: #f0f0f0a9;
  max-height: 100vh;
  position: fixed;
}

table, th, td {
  empty-cells: hide;
}

td {
  color: #fff;
  font-size: x-large;
}

th, td {
  text-align: center;
  border-collapse: collapse;
  background: #edeeeed6;
  border: 2px solid #2e2e2e;
}

th {
  background: #a8a8a8d6;
  padding: 15px;
}

input[type="number"] {
  width: 2em;
}

.control_button {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 20%;
  margin: 2%;
  display: inline-block;
}

